<template>
<div class="panel panel-info">
    <div class="panel-body">
        <div>报警信息管理</div>
        <div class="buttonbox">
            <button type="button" class="btn btn-danger" @click="deleteData" v-if="$api.opreate.hasLoginComponents('sys:alert:delete')">删除</button>
        </div>
        <tables :tableSelectData="tableSelectData"></tables>
    </div>
</div>
</template>

<script>
import tables from "@/components/table.vue";
export default {
    data: function () {
        return {
            tableSelectData: {
                selectTableurl: "Alarm/selectAlarmInfo.action",
                pageloginData: {
                    id: this.stateid
                },
                filterName: []
            }
        }
    },
    computed: {
        checkData() {
            return this.$store.getters.GETUSERTABLEPAGECHECKDATA;
        }
    },
    components: {
        tables,
    },
     mounted(){
        this.$store.commit("UPDATETABLESELECTDATA",this.tableSelectData);
    },
    methods: {
        // 删除按钮按下
        deleteData() {
            var vm = this;
            var checkData = vm.$store.getters.GETUSERTABLEPAGECHECKDATA; // 获取当前页面的
            if (vm.$api.opreate.TESTcheck("删除")) {
                //询问框
                layer.confirm(
                    "是否删除", {
                        btn: ["确认", "取消"] //按钮
                    },
                    function () {
                        var datas = {
                            id: checkData[0].id, //需要删除行的数据id，需要根据行id进行相应修改 如选中行数据id为 userid,则此处需要改为checkData[0].userid
                            states: {
                                id: vm.stateid
                            }
                        };
                        vm.$api.http.opreateHttp(
                            "Alarm/deleteAlarmInfo.action",
                            datas,
                            "删除"
                        );
                    }
                );
            }
        },
    }
}
</script>

<style>

</style>



